.department-info {
  margin-left: 300px;
}

.department-page h2 {
  color: var(--color-7);
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.08rem;
  padding: 30px;
  text-align: center;
}

.department-members {
  border-top: 1px solid grey;
  margin: 0 auto;
  background-color: #fafafa;
  padding: 15px;
  width: 65%;
}

.department-container-title {
  display: inline-flex;
}

.department-container-teams {
  display: block;
}

.department-container-title h3 {
  color: var(--color-7);
  margin: 0 !important;
  padding-bottom: 10px;
}

.department-information-container {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}

.department-information-container h2 {
  padding: 0;
  padding-left: 20px;
}

.department-container-news {
  overflow: auto;
  height: 300px;
  margin-left: 15px;
}

.f-card {
  background-color: white;
  /*height:400px;*/
  border: 1px solid grey;
  width: 502px;
  border: 1px solid #d0d1d5;
  border-radius: 3px;
  margin: auto;
  margin-bottom: 10px;
  padding: 12px;

  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3); /* Not original, just a test */
}

.news-header {
  margin-bottom: 17px;
}

.co-logo {
  /*display:block;*/
  float: left;
  margin-right: 8px;

  width: 40px;
  height: 40px;
}

.co-name > a {
  font-family: Helvetica;
  font-size: 14px;
  font-weight: bold;
  line-height: 1.38;
  color: #365899;
  text-decoration: none;

  margin-bottom: 2px;
}

.co-name > a:hover {
  text-decoration: underline;
}

.time {
  font-family: Helvetica;
  font-size: 12px;
  color: #90949c;
}

.time > a {
  color: #90949c;
  text-decoration: none;
}

.time > a:hover {
  text-decoration: underline;
}

.options {
  font-family: Helvetica;
  font-size: 12px;
  color: #e5e5e5;
  float: right;
}

.options:hover {
  color: black; /* Fallback */
  color: rgba(0, 0, 0, 0.3);
}

.content {
  clear: both;
  font-family: Helvetica, sans-serif;
  font-size: 14px;
  line-height: 1.38;
}

.reference {
  width: 476px;
}
.reference-thumb {
  display: block;
  width: 476px;
  height: 249px;
}

.reference-content {
  border: 2px solid black;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-top: 0;
  padding: 10px 12px 10px 12px;
}

.reference:hover .reference-content {
  border-color: black; /* Fallback */
  border-color: rgba(0, 0, 0, 0.15);
}

.reference-title {
  font-family: Georgia;
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;

  margin-bottom: 5px;
}

.reference-subtitle {
  font-family: Helvetica;
  font-size: 12px;
  line-height: 16px;
}

.reference-font {
  color: #90949c;
  font-family: Helvetica;
  font-size: 11px;
  line-height: 11px;
  text-transform: uppercase;

  padding-top: 9px;
}

.social {
  margin-top: 12px;
}
.social-buttons {
  color: #7f7f7f;
  font-family: Helvetica;
  font-size: 12px;
  font-weight: bold;
  line-height: 14px;

  border-top: 1px solid #e5e5e5;
  padding-top: 4px;
}

.social-buttons span {
  font-size: 12px;
  margin-right: 20px;
  padding: 4px 4px 4px 0;
}

.social-buttons span:hover {
  text-decoration: underline;
  cursor: pointer;
}

.social-buttons span i {
  padding-right: 4px;
}
